import React from 'react'

function ItemSalad({data}) {
    return (
        <div className="container d-flex">
            <div className="col-6 align-self-center">
                <h1 className="mb-5 text-danger display-4">{data[15].name}</h1>
                <p>{data[15].description}</p>
            </div>
            <img className="col-6 align-self-center" src={data[15].img_src}/>
        </div>
    )
}

export default ItemSalad;