import React, {useEffect, useState} from "react";
const Location = () => {
    const [data, setData] = useState([
        
    ]);

    useEffect(() => {
        getData();
    },[]);

    // const loadData = async () => {
    //     await fetch("https://mathi9728.github.io/bkjsonapi/Burger_King_project_data.JSON")
    //     .then(response => response.json())
    //     .then(receivedData => setData(receivedData));
    // }
    async function getData() {
        const api ="https://api.jsonbin.io/b/617922c0aa02be1d445f82a1";
        const result = await fetch(api);
        const getResult = await result.json();
        setData(getResult);
    }
console.log(data)
    return (
        <div >
            <div className="container">
                {data.map(place => (
                    <div key={place.id} className="d-flex flex-column">
                        <h3 className="mt-5 text-danger">{place.name}</h3>
                        <div className="mt-2 mb-2">
                            {place.location}
                        </div>
                        <div className="mt-1 mb-1 text-warning">
                            {place.hours}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )
}

export default Location;