import React from 'react'

const Cappuccino = ({data}) => {
    return (
        <div className="container d-flex">
            <div className="col-6 align-self-center">
                <h1 className="mb-5 text-danger display-4">{data[29].name}</h1>
                <p>{data[29].description}</p>
            </div>
            <img className="col-6 align-self-center" src={data[29].img_src}/>
        </div>
    )
}

export default Cappuccino;
