import React from 'react'

function MushroomVeggieBurger({data}) {
    return (
        <div className="container d-flex">
            <div className="col-6 align-self-center">
                <h1 className="mb-5 text-danger display-4">{data[14].name}</h1>
                <p>{data[14].description}</p>
            </div>
            <img className="col-6 align-self-center" src={data[14].img_src}/>
        </div>
    )
}

export default MushroomVeggieBurger;