import React from 'react'

const WhooperJr = ({data}) => {
    return (
        <div className="container d-flex">
            <div className="col-6 align-self-center">
                <h1 className="mb-5 text-danger display-4">{data[2].name}</h1>
                <p>{data[2].description}</p>
            </div>
            <img className="col-6 align-self-center" src={data[2].img_src}/>
        </div>
    )
}

export default WhooperJr;