import React from 'react'

function SingleBBQBeefacon({data}) {
    return (
        <div className="container d-flex">
            <div className="col-6 align-self-center">
                <h1 className="mb-5 text-danger display-4">{data[3].name}</h1>
                <p>{data[3].description}</p>
            </div>
            <img className="col-6 align-self-center" src={data[3].img_src}/>
        </div>
    )
}

export default SingleBBQBeefacon;