import React from 'react'

const Tendergrill = ({data}) => {
    return (
        <div className="container d-flex">
            <div className="col-6 align-self-center">
                <h1 className="mb-5 text-danger display-4">{data[11].name}</h1>
                <p>{data[11].description}</p>
            </div>
            <img className="col-6 align-self-center" src={data[11].img_src}/>
        </div>
    )
}

export default Tendergrill;
