import React from 'react'

const DoubleCheeseBurger = ({data}) => {
    return (
        <div className="container d-flex">
            <div className="col-6 align-self-center">
                <h1 className="mb-5 text-danger display-4">{data[8].name}</h1>
                <p>{data[8].description}</p>
            </div>
            <img className="col-6 align-self-center" src={data[8].img_src}/>
        </div>
    )
}

export default DoubleCheeseBurger;
