import React from 'react'
import FoodNav from '../../components/FoodNav';
const Dessert = () => {
    return (
        <div className="container">
            <FoodNav />
            <div className="row">
                <a href="/dessert/applepie" className="col-md-4 d-flex justify-content-center flex-column text-decoration-none text-dark mb-5 text-decoration-none text-dark mb-5" style={{cursor: "pointer"}}>
                    <img src="https://www.burgerking.com.my/upload/image/Product/41/Apple%20Pie.jpg" className="w-50 h-75 align-self-center"/>
                    <h3 className="text-center w-75 h-75 align-self-center">Apple Pie</h3>
                </a>
            
                <a href="/dessert/hershey" className="col-md-4 d-flex justify-content-center flex-column text-decoration-none text-dark mb-5 text-decoration-none text-dark mb-5" style={{cursor: "pointer"}}>
                    <img src="https://www.burgerking.com.my/upload/image/Product/118/Hershey%20Pie.jpg" className="w-50 h-75 align-self-center"/>
                    <h3 className="text-center w-75 h-75 align-self-center">HERSHEYS Sundae Pie</h3>
                </a>
            </div>
        </div>
    )
}

export default Dessert;
