// import React from 'react'
import React from "react";

function ItemWhooper({data}) {

    return (
        <div className="container d-flex">
            <div className="col-6 align-self-center">
                <h1 className="mb-5 text-danger display-4">{data[0].name}</h1>
                <p>{data[0].description}</p>
            </div>
            <img className="col-6 align-self-center" src={data[0].img_src}/>
            {/* <p>{value.data.category}</p> */}
        </div>
    )
}

export default ItemWhooper;
