import React from 'react'
import FoodNav from '../../components/FoodNav';
const Sides = () => {
    return (
        <div>
            <div className="container">
                <FoodNav />
                <div className="row">
                    <a href="/sides/onionrings" className="col-md-4 d-flex justify-content-center flex-column text-decoration-none text-dark mb-5 text-decoration-none text-dark mb-5" style={{cursor: "pointer"}}>
                        <img src="https://www.burgerking.com.my/upload/image/Product/17/Onion%20Ring%205pcs.jpg" className="w-50 h-75 align-self-center"/>
                        <h3 className="text-center w-75 h-75 align-self-center">Onion Rings</h3>
                    </a>
                    <a href="/sides/nuggets" className="col-md-4 d-flex justify-content-center flex-column text-decoration-none text-dark mb-5 text-decoration-none text-dark mb-5" style={{cursor: "pointer"}}>
                        <img src="https://www.burgerking.com.my/upload/image/Product/21/Nuggets%206pcs.jpg" className="w-50 h-75 align-self-center"/>
                        <h3 className="text-center w-75 h-75 align-self-center">6 Pcs Nuggets</h3>
                    </a>
                    <a href="/sides/frenchfries" className="col-md-4 d-flex justify-content-center flex-column text-decoration-none text-dark mb-5 text-decoration-none text-dark mb-5" style={{cursor: "pointer"}}>
                        <img src="https://www.burgerking.com.my/upload/image/Product/18/French%20Fries.jpg" className="w-50 h-75 align-self-center"/>
                        <h3 className="text-center w-75 h-75 align-self-center">French Fries</h3>
                    </a>
                </div>
            </div>
        </div>
    )
}

export default Sides;
